/*Reset all default properties from html elements*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  background: transparent;
  border: 0;
  font-size: 100%;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline; }

body {
  line-height: 1;
  -ms-overflow-style: scrollbar; }

button {
  margin: 0; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: '';
  content: none; }

:focus {
  outline: 0; }

ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: collapse;
  border-spacing: 0; }

input[type='search']::-webkit-search-cancel-button {
  display: none; }

input[type='search']::-ms-clear {
  display: none; }

input[type=text]::-ms-clear {
  display: none; }

input[type=password]::-ms-reveal {
  display: none; }

@font-face {
  font-display: swap;
  font-family: 'HP Simplified';
  src: url("../fonts/HPSimplified_Rg.ttf") format("truetype"); }

@font-face {
  font-display: swap;
  font-family: 'HP Simplified Light';
  src: url("../fonts/HPSimplified_Lt.ttf") format("truetype"); }

